<script>
// import echarts from 'echarts'
import * as echarts from 'echarts'
/* import _ from 'loadash' */
export default {
  data () {
    return {
      headInfo: {
        /* spendtime: '20h',
        cleanspace: '100GB',
        cleantimes: '1000',
        machines: '999' */
      },
      toplist: [],
      chartsInfo: {
        title: '',
        xAxis_data: [],
        series_data: []
      }
    }
  },
  created () {
    this.getHeadInfo()
    this.getTopList()
    /* this.getChartsData() */
  },
  mounted () {
    this.getChartsData()
    /* var myChart = echarts.init(document.getElementById('main'))
    var option = {
      title: {
        text: this.chartsInfo.title
      },
      xAxis: {
        type: 'category',
        data: this.chartsInfo.xAxis_data
        data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
      },
      yAxis: {
        type: 'value'
      },
      series: [{
        data: [150, 230, 224, 218, 135, 147, 260],
        data: this.chartsInfo.series_data,
        type: 'line'
      }]
    } */
    /* var data = {
      xAxis: {
        data: res.data.xAxis_data
      },
      series: [{
        data: res.data.series_data
      }]
    } */
    /* const result = _.merge(option, data) */
    /* const result = Object.assign(option, data) */
    /* myChart.setOption(option) */
  },
  methods: {
    logout () {
      window.sessionStorage.clear()
      this.$router.push('/login')
    },
    async getHeadInfo () {
      try {
        const { data: res } = await this.$http.get('home_head')
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        console.log(res.data)
        this.headInfo = res.data
      } catch (e) {
        const erroInfo = e.toString()
        if (erroInfo.indexOf('status code 401') !== -1) {
          console.log('认证已过期,请重新登录')
          this.logout()
          return this.$message.error('认证已过期,请重新登录')
        }
        return this.$message.error(erroInfo)
      }
    },
    async getTopList () {
      try {
        const { data: res } = await this.$http.get('home_top_list')
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        console.log(res.data)
        this.toplist = res.data.toplist
      } catch (e) {
        const erroInfo = e.toString()
        if (erroInfo.indexOf('status code 401') !== -1) {
          console.log('认证已过期,请重新登录')
          this.logout()
          return this.$message.error('认证已过期,请重新登录')
        }
        return this.$message.error(erroInfo)
      }
    },
    async getChartsData () {
      const myChart = echarts.init(document.getElementById('main'))
      myChart.resize()
      try {
        const { data: res } = await this.$http.get('home_charts_month')
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        myChart.setOption({
          title: {
            text: res.data.title
          },
          xAxis: {
            type: 'category',
            data: res.data.xAxis_data
            /* data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'] */
          },
          yAxis: {
            type: 'value'
          },
          series: [{
            /* data: [150, 230, 224, 218, 135, 147, 260], */
            data: res.data.series_data,
            type: 'line'
          }]
        })
        /* this.chartsInfo.xAxis_data = res.data.xAxis_data
        this.chartsInfo.series_data = res.data.series_data
        this.chartsInfo.title = res.data.title */
        console.log(res.data)
        console.log(this.chartsInfo)
      } catch (e) {
        const erroInfo = e.toString()
        if (erroInfo.indexOf('status code 401') !== -1) {
          console.log('认证已过期,请重新登录')
          this.logout()
          return this.$message.error('认证已过期,请重新登录')
        }
        return this.$message.error(erroInfo)
      }
    }
  }
}
